#drum-machine {
  display: grid;
  grid-template-columns: 1fr;
  grid-gap: 1rem;
  min-width: 500px;
}

#drum-machine #drums {
  background: #f5f5f555;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: repeat(3, 1fr);
  grid-gap: 0.5rem;
  padding: 1rem;
  border: 1px solid white;
  border-radius: 10px;
  justify-items: center;
}

#drum-machine #display {
  text-align: center;
  background: #f5f5f510;
  border: 1px solid white;
  width: 100%;
  border-radius: 10px;
  height: 3rem;
}

.drum-pad {
  background: -webkit-gradient(linear, left top, left bottom, from(#e66465), to(#9198e5));
  background: -webkit-linear-gradient(#e66465, #9198e5);
  background: -o-linear-gradient(#e66465, #9198e5);
  background: linear-gradient(#e66465, #9198e5);
  height: 5rem;
  width: 5rem;
  font-size: xx-large;
  border-radius: 5px;
  border: none;
  -webkit-transform: scale(1);
      -ms-transform: scale(1);
          transform: scale(1);
  -webkit-transition: all 0.25s cubic-bezier(0.175, 0.885, 0.32, 1.275);
  -o-transition: all 0.25s cubic-bezier(0.175, 0.885, 0.32, 1.275);
  transition: all 0.25s cubic-bezier(0.175, 0.885, 0.32, 1.275);
  border: 1px solid white;
  cursor: pointer;
}

.drum-pad:active,
.button-active {
  -webkit-transform: scale(1.1);
      -ms-transform: scale(1.1);
          transform: scale(1.1);
  background: -webkit-gradient(linear, left top, left bottom, from(#9198e5), to(#e66465));
  background: -webkit-linear-gradient(#9198e5, #e66465);
  background: -o-linear-gradient(#9198e5, #e66465);
  background: linear-gradient(#9198e5, #e66465);
}



