.nav-grid {
  display: grid;
  grid-template-columns: repeat(6, 100px) 1fr;
  grid-template-rows: 64px;
  background: rgb(0, 95, 158);
}

.nav-item {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  /* border: solid whitesmoke 1px; */
  color: whitesmoke;
  text-decoration: none;
  padding: 8px;
  height: 64px;
  -ms-flex-item-align: center;
      align-self: center;
}

.nav-item:hover {
  opacity: 0.5;
}

.selected {
  font-weight: bold;
  background: rgb(0, 68, 114);
}

.git-hub-link {
  margin: 0 0 0 auto;
}



