html {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  background: rgb(0, 51, 85);
  color: whitesmoke;
}

*,
*:before,
*:after {
  -webkit-box-sizing: inherit;
          box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

html,
body,
#root {
  height: 100%;
}



