#quote-box {
  display: grid;
  grid-template-columns: 1fr 1fr;
  width: 450px;
  border: solid whitesmoke 1px;
  border-radius: 5px;
  padding: 32px;
  background: whitesmoke;
  -webkit-box-shadow: 0 10px 6px -6px gray;
          box-shadow: 0 10px 6px -6px gray;
}

#quote-box #text {
  grid-column: 1 / -1;
  font-size: 24px;
  text-align: start;
  -webkit-transition: all 1.5s ease;
  -o-transition: all 1.5s ease;
  transition: all 1.5s ease;
  margin: 0;
}

#quote-box #author {
  grid-column: 1 / -1;
  justify-self: end;
  -webkit-transition: all 1.5s ease;
  -o-transition: all 1.5s ease;
  transition: all 1.5s ease;
}

#quote-box #new-quote {
  grid-column: 2 / -1;
  justify-self: end;
  -ms-flex-item-align: center;
      align-self: center;
}

#quote-box #tweet-quote {
  grid-column: 1 / 2;
  justify-self: start;
  -ms-flex-item-align: center;
      align-self: center;
}

.btn {
  font-size: normal;
  border: 0;
  color: whitesmoke;
  padding: 12px;
  border-radius: 5px;
  outline: none;
  cursor: pointer;
  text-decoration: none;
  opacity: 0.8;
  -webkit-transition: all 0.5s ease;
  -o-transition: all 0.5s ease;
  transition: all 0.5s ease;
}

.btn:hover {
  opacity: 1;
}



