.main-grid {
  display: grid;
  grid-template-columns: 200px auto 200px;
  grid-template-rows: auto 1fr;
  height: 100%;
}

.header {
  grid-column: 2;
}

.content {
  display: grid;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-line-pack: center;
      align-content: center;
  grid-column: 2;
  grid-row: 2;
}



