.calc-grid {
  display: grid;
  padding: 1rem;
  grid-template-areas:
    'calc-window calc-window calc-window calc-window'
    '. . . clear'
    'seven eight nine divide'
    'four five six multiply'
    'one two three subtract'
    'zero decimal equals add';
  grid-gap: 1rem;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-line-pack: center;
      align-content: center;
  border: 2px solid white;
  border-radius: 5px;
}

.calc-btn {
  background: lightseagreen;
  height: 5rem;
  width: 7rem;
  font-size: xx-large;
  border-radius: 5px;
  border: none;
  -webkit-transform: scale(1);
      -ms-transform: scale(1);
          transform: scale(1);
  -webkit-transition: all 0.25s cubic-bezier(0.175, 0.885, 0.32, 1.275);
  -o-transition: all 0.25s cubic-bezier(0.175, 0.885, 0.32, 1.275);
  transition: all 0.25s cubic-bezier(0.175, 0.885, 0.32, 1.275);
  border: 1px solid white;
  cursor: pointer;
}

.calc-btn:active {
  -webkit-transform: scale(0.95);
      -ms-transform: scale(0.95);
          transform: scale(0.95);
  background: darkblue;
  color: white;
}

#calc-window {
  display: grid;
  grid-column: 1 / -1;
  border-radius: 5px;
  border: 2px solid white;
  height: 5.5rem;
}
#display {
  padding: 8px;
  justify-self: end;
  -ms-flex-item-align: end;
      align-self: end;
  font-family: 'Courier New', Courier, monospace;
  font-size: xx-large;
}
#input {
  padding: 8px;
  justify-self: end;
}

#zero {
  grid-area: zero;
}
#one {
  grid-area: one;
}
#two {
  grid-area: two;
}
#three {
  grid-area: three;
}
#four {
  grid-area: four;
}
#five {
  grid-area: five;
}
#six {
  grid-area: six;
}
#seven {
  grid-area: seven;
}
#eight {
  grid-area: eight;
}
#nine {
  grid-area: nine;
}
#multiply {
  background: steelblue;
  grid-area: multiply;
}
#divide {
  background: steelblue;
  grid-area: divide;
}
#add {
  background: steelblue;
  grid-area: add;
}
#subtract {
  grid-area: subtract;
  background: steelblue;
}
#decimal {
  grid-area: decimal;
}
#clear {
  grid-area: clear;
  background: coral;
}
#equals {
  grid-area: equals;
  background: seagreen;
}



